.sidebar-menu {
  position: fixed;
}

.sidebar-menu__item {
  font-size: 19px;
  color: #0f141a;
  display: flex;
  align-items: center;
  font-weight: 700;
  padding: 10px;
}

.sidebar-menu__item:hover {
  background: rgba(29, 161, 242, 0.1);
  color: #1da0f2;
  border-radius: 20px;
  cursor: pointer;
}

.sidebar-menu__item--active {
  color: #1da0f2;
}

.sidebar-menu__item-icon {
  height: 26px;
  margin-right: 20px;
}

.sidebar-menu__sign-out {
  margin-left: 55px;
}

.sidebar-menu__item--active .sidebar-menu__item-icon {
  filter: invert(52%) sepia(90%) saturate(1952%) hue-rotate(177deg)
    brightness(95%) contrast(100%);
}
